.swiper-slide {
  // position: relative;

  height: 450px;

  &::before {
    content: '';
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 0;
    height: 100%;
    background-color: rgba(#333, 0.2);
  }

  &__content {
    // // position: absolute;
    // top: 50%;
    // left: 50%;
    // margin: 0 auto;
    padding: 140px;
    // z-index: 999;
    color: #fff;
    z-index: 10;
    transform: translate(-1%, -1%);
  }

  &__title {
    font-size: 48px;
    margin-bottom: 10px;
  }

  &__desc {
    font-size: 16px;
    margin-bottom: 20px;
  }

  &__link {
    display: inline-block;
    // text-align: center;
    font-weight: 700;
    font-size: 16px;
    padding: 15px 45px;

    color: #000;
    background-color: #fff;
  }

  &__image {
  }
}
