@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

html,
body {
  font-size: 14px;
  font-weight: 400;
  overflow-x: hidden;
}

/* копия */
body {
  overflow-anchor: none;
  margin: 0;
}

.container {
  width: 100%;
  max-width: 1200px;
  padding-right: 5px;
  padding-left: 5px;
  margin-right: auto;
  margin-left: auto;
  /* text-align: center; */
}

/* скопировал */
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  margin: 0 auto;
  padding: 0 calc(5px + (15 - 5) * (100vw - 320px) / (1200 - 320));
  max-width: 1200px;
  @media screen and (max-width: 960) {
  }
}

.header{border-bottom:1px solid rgba(0,0,0,0.1);margin-bottom:20px}.header__news{height:30px;padding:8px 16px;color:#666;text-align:center;background:#f2f2f2;font-size:10px}.header__container{height:55px;display:flex;justify-content:space-between;align-items:center}.logo{display:inline-block;transform:scale(0.8)}.nav__list{display:flex;justify-content:space-between;align-items:center}.nav__item:not(:last-child){margin-right:16px;padding:10px}.nav__link{text-decoration:none;color:#000}@media screen and (max-width: 991px){.nav{display:none}}.user-nav{min-width:100px;max-width:150px;display:flex;justify-content:space-between}.user-nav .user-nav__btn{outline:none;border:none;background-color:transparent}.bar_icon{color:red}.menu-bars{font-size:2rem;background:none;display:none;z-index:11}@media screen and (max-width: 961px){.menu-bars{display:block;z-index:10}}.overlay{height:100vh;width:100vw;position:absolute;top:0;left:0;opacity:0.6;z-index:9;background:#1a1a1a}.nav-menu{width:250px;height:100vh;display:flex;justify-content:center;position:fixed;top:0;left:-100%;transition:850ms;z-index:10}.nav-menu.active{left:0;transition:350ms}.nav-text{display:flex;justify-content:flex-start;align-items:center;padding:8px 0px 8px 16px;list-style:none;height:60px}.nav-text a{text-decoration:none;color:#000;font-size:18px;width:95%;height:100%;display:flex;align-items:center;padding:0 16px;border-radius:4px}.nav-text a:hover{background-color:#1a83ff}.nav-menu-items{width:100%;background-color:#fff}.navbar-toggle{background-color:#fff;width:100%;height:80px;display:flex;z-index:13;justify-content:flex-start;align-items:center}.login{padding:20px 10px;position:fixed;width:660px;min-height:266px;top:50%;left:0;right:0;margin:0 auto;background-color:#333;z-index:14;opacity:1;transform:translate3d(0px, -50.09%, 0px);background:#fff}.login h2{text-align:center;margin-bottom:10px}.login p{text-align:center;margin-bottom:10px}.login input[type='text'],.login input[type='password']{width:100%;padding:12px 20px;margin:8px 0;display:inline-block;border:1px solid #ccc}.login button{background-color:#333;color:white;padding:14px 20px;margin:8px 0;border:none;cursor:pointer;width:100%}.login button:hover{opacity:0.8}

.hero{margin-bottom:20px}.hero__container{display:flex}.hero__item{padding:0 calc(5px + (10 - 5) * (100vw - 320px) / (1200 - 320));width:100%;text-align:start;overflow:hidden}.hero__item-image{display:block;min-height:300px;padding:0 0 100% 0;position:relative;margin-bottom:10px}.hero__item-img{position:absolute;top:0;left:0;height:100%;width:100%;object-fit:cover}.hero__item-title{font-size:28px;margin-left:10%;margin-bottom:2%;font-weight:400}.hero__item-link{text-decoration:underline;margin-left:10%;letter-spacing:1.6px}

.product{position:relative;display:flex;flex-direction:column;justify-content:space-between;align-items:start;min-height:200px;max-width:250px;margin-right:15px;margin-bottom:20px}.product:nth-child(4n){margin-right:0}.product__link img{width:100%;height:100%;object-fit:cover}.product__title{font-weight:400;padding:5px 0}.product__title a::before{content:'';position:absolute;top:0;left:0;bottom:0;right:0}.product__desc{opacity:0.6;padding:5px 0}.product__price{padding:5px 0}

.banner_link{display:block;margin:auto;margin-bottom:20px}.banner_link img{width:100%;height:100%;object-fit:cover}

.popular__items{display:flex;justify-content:center;align-items:center;flex-wrap:wrap;margin-bottom:80px}.popular__title{text-align:center;font-size:28px;margin-bottom:40px}

.jacket{background-image:url(/clothing-shop/static/media/jacket.b1da8437.jpg)}.swiper-slide{height:450px}.swiper-slide::before{content:'';position:absolute;display:block;top:0;left:0;width:100%;z-index:0;height:100%;background-color:rgba(51,51,51,0.2)}.swiper-slide__content{padding:140px;color:#fff;z-index:10;transform:translate(-1%, -1%)}.swiper-slide__title{font-size:48px;margin-bottom:10px}.swiper-slide__desc{font-size:16px;margin-bottom:20px}.swiper-slide__link{display:inline-block;font-weight:700;font-size:16px;padding:15px 45px;color:#000;background-color:#fff}

.filter__input{position:relative}.filter__input-input{width:100%;height:30px;padding:8px;padding-right:20px;border:1px solid #aaa}.filter__input-btn{position:absolute;font-size:16px;color:#aaa;top:7px;right:4px;outline:none;border:none;background-color:transparent}.catalog-filters{padding-top:27px;max-width:240px;padding-bottom:27px;padding-left:20px}.catalog-filter{padding-top:27px;border-top:1px solid #000}.catalog-filter__items{max-height:300px;overflow:scroll}.catalog-filter__item{margin-bottom:10px}.catalog-filter__top{align-items:center;justify-content:space-between;margin-bottom:10px}.catalog-filter__caption{margin-bottom:10px;display:flex;align-items:center;justify-content:space-between;cursor:pointer}.catalog-filter__title{display:inline-block;font-weight:700;font-size:13px;text-transform:uppercase;margin-right:5px}.catalog-filter__toggle{display:inline-block;position:relative;font-size:13px;color:#333}.catalog-filter__bottom{border-bottom:1px solid #333}.quantity{display:inline-flex;align-items:center;border-radius:25px;padding:0 5px;height:16px;font-weight:700;font-size:10px;color:#fff;background-color:#333}.custom-checkbox{display:inline-block;display:flex;align-items:center;cursor:pointer}.custom-checkbox__text{color:#aaa;padding-left:10px;font-size:13px}.custom-checkbox__control{display:inline-block;position:relative;width:16px;height:16px;border:2px solid #aaa}.custom-checkbox__control::before{content:'';position:absolute;display:block;top:50%;left:50%;transform:translate(-50%, -50%);width:7px;height:7px;background-color:#333;opacity:0;transition:all 0.2s}.custom-checkbox__input{display:none}.custom-checkbox__input:checked+.custom-checkbox__control::before{opacity:1}.custom-checkbox__input:checked ~ .custom-checkbox__text{color:#000}

.products__items{display:grid;grid-column-gap:24px;-webkit-column-gap:24px;column-gap:24px;grid-template-rows:1fr;grid-template-columns:240px auto}.product__items-cards{display:flex;flex-wrap:wrap}

.footer{border-top:1px solid rgba(0,0,0,0.2);background-color:rgba(0,0,0,0.02)}.footer__content{padding-top:60px;padding-bottom:50px}.footer__top{display:flex;justify-content:space-around;flex-wrap:wrap}@media screen and (max-width: 700px){.footer__top{flex-direction:column;justify-content:space-between;align-items:center}}.footer__column{flex:1 1;margin-right:10px;margin-bottom:20px}.footer__item:not(:last-child){margin-right:2%;margin-bottom:10px}.footer__link{font-size:13px}.footer__title{font-weight:500;font-size:16px;margin-bottom:20px}.footer__form{width:350px}.footer__social{font-size:20px}.footer__social-list{display:flex}.footer__social-item{margin-right:10px}.footer__bottom{display:flex;align-items:center;justify-content:space-between}.subscribe__form{position:relative;margin-bottom:12px}.subscribe__form-title{font-weight:400;font-size:13px;margin-bottom:10px}.subscribe__form-btn{position:absolute;right:0;bottom:4px;z-index:2;font-weight:500;font-size:13px;outline:none;border:none;background:transparent}.subscribe__form-field{display:block;width:100%}.subscribe__form-field input{width:100%;border:none;height:20px;font-size:13px;padding-right:80px;font-weight:500;border-bottom:1px solid #747474}.subscribe__form-field input::-webkit-input-placeholder{opacity:0.5;color:#454545}.subscribe__form-field input:-ms-input-placeholder{opacity:0.5;color:#454545}.subscribe__form-field input::-ms-input-placeholder{opacity:0.5;color:#454545}.subscribe__form-field input::placeholder{opacity:0.5;color:#454545}

